.Navbar {
  background-color: black;
  width: 100%;
  height: 80px;
  font-family: sans-serif;
  display: flex;
  color: orange;
  border-bottom: 3px solid white;
}
.Navbar .leftSide {
  flex: 60% 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Navbar .leftSide i {
  display: none;
  margin-left: 20px;
}
.Navbar .leftSide #hidden {
  display: flex;
}
.Navbar .leftSide .links {
  max-height: 80px;
  text-transform: uppercase;
  font-weight: bold;
}
.Navbar .leftSide .links a {
  text-decoration: none;
  color: orange;
  font-size: 20px;
  margin-left: 15px;
}
.Navbar .rightSide {
  flex: 40% 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Navbar .rightSide input {
  width: 220px;
  height: 30px;
  border: none;
  border-radius: 4px;
  font-size: 24px;
  padding-left: 15px;
  color: #636e72;
}
.Navbar .rightSide button {
  height: 32px;
  width: 70px;
  font-size: 16px;
  border-radius: 4px;
}
.rightSide h1 {
  display: flex;
  justify-content: center;
  letter-spacing: 2px;
}
@media only screen and (max-width: 850px) {
  .Navbar .leftSide .links a {
    font-size: 20px;
  }
}
@media only screen and (max-width: 750px) {
  .Navbar .leftSide .links a {
    font-size: 15px;
  }
}
@media only screen and (max-width: 600px) {
  .Navbar .leftSide i {
    display: flex;
  }
  .Navbar .leftSide .links {
    display: none;
  }
  .Navbar .leftSide #hidden {
    position: absolute;
    left: 0px;
    top: 80px;
    min-height: 250px;
    width: 100%;
    background-color: #000;
    z-index: 11;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .Navbar .leftSide #hidden a {
    margin: 10px;
    font-size: 25px;
    color: orange;
    z-index: 100;
  }
}


