.slider {
  height: 340px;
  overflow: hidden;
  position: relative;
  top: 65px;
  width: 100%;
  border-radius: 6px;
  background-color: transparent;
}
.slider::before,
.slider::after {
  content: "";
  height: 100%;
  position: absolute;
  width: 100px;
  z-index: 2;
}
.slider::after {
  right: 0;
  top: 0;
  -webkit-transform: rotateZ(180deg);
  transform: rotateZ(180deg);
}
.slider::before {
  left: 0;
  top: 0;
}
.slider .slide-track {
  -webkit-animation: scroll 80s linear infinite;
          animation: scroll 80s linear infinite;
  display: flex;
  width: calc(340px * 18);
}
.slider .slide-track:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.slider .slide {
  height: 260px;
  width: 260px;
}
